*{
    margin: 0 auto;
    padding: 0;
    list-style: none;
    text-decoration: none;
    color: #ffffff;
    box-sizing: border-box;
    font-family: 'Work Sans';
}

.container{
    min-height: 100vh;
    background-color: #1e6f5c;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.mainmenu li{
    font-size: 50px;
}

.mainmenu ul{
    display: flex;
    flex-direction: column;
    align-items: center;
}